
.discuss-release {
    width:100%;
    height:7.5rem;
    margin:0.1rem 0;
    text-align:center;
    font-size:0.5rem;
}
.discuss-content {
    text-align: left;
}
.discuss-topic {
    width:74%;
    height:1rem;
    border:none;
    border:0.01rem solid #999;
    border-radius:0.1rem;
    margin:0.1rem 0;;
    padding-left:0.4rem;
    font-size:0.45rem;
    color:#666;
}
textarea {
    width:70%;
    border:none;
    border:0.01rem solid #999;
    border-radius:0.1rem;
    margin-top: 0.1rem;
    padding:0.1rem 0.4rem;
    font-size:0.45rem;
    color:#666;
    vertical-align: top;
}
.discuss-release button {
    width:95%;
    height:1.2rem;
    border:none;
    border-radius:0.2rem;
    margin:0.2rem 0;
    font-size:0.5rem;
    color:#fff;
    background-color:#ff7043;
}



